import { PureComponent, ReactElement } from 'react';
import _ from 'lodash';
import tokenHelper from '../../../helper/tokenHelper';
import swapHelper from '../../../helper/swapHelper';
import style from './PriceImpact.module.scss';

interface IStoreProps {}
interface IHOCProps {}
interface IOwnProps {
  priceImpact: number;
  priceMarket: number;
}
interface IOwnState {}

class PriceImpact extends PureComponent<
  IOwnProps & IHOCProps & IStoreProps,
  IOwnState
> {
  state: IOwnState;

  constructor(props: IOwnProps & IHOCProps & IStoreProps) {
    super(props);

    this.state = {};
  }

  render(): ReactElement {
    const { priceImpact, priceMarket } = this.props;

    return (
      <div
        className={
          'd-flex justify-content-between align-items-center fk-swap-text-info ' +
          style.wrap
        }
      >
        <div>Price impact</div>
        <div
          className={swapHelper.getClassWarningPriceImpact(priceImpact * 100)}
        >
          {priceImpact !== -1
            ? `($${tokenHelper.roundCurrency(priceMarket)}) ${_.round(
                priceImpact * 100,
                2
              )}%`
            : 'UNKNOW'}
        </div>
      </div>
    );
  }
}

export default PriceImpact;
